import BaseTransaction from './Base.js';
class EmailStatusTransaction extends BaseTransaction {
  constructor(oktaAuth, options) {
    super(oktaAuth, options);
    const {
      res
    } = options;
    const {
      id,
      profile,
      expiresAt,
      status
    } = res;
    this.id = id;
    this.expiresAt = expiresAt;
    this.profile = profile;
    this.status = status;
  }
}
export { EmailStatusTransaction as default };
